import React, { Component } from "react";
import imgBase from "img/base.png";
import imgCamera from "img/camera.png";
import imgGrid from "img/grid.png";
import img218_1 from "img/218/1.png";
import example from "img/example.jpg";
import font1 from "img/font/1.png"; //Noto Sans CJK KR
import font1t from "img/font/1t.png"; //Noto Sans CJK KR
import font2 from "img/font/2.png"; //Sandoll 격동고딕
import font2t from "img/font/2t.png"; //Sandoll 격동고딕
import font3 from "img/font/3.png"; //Sandoll 고딕Neo1유니코드
import font3t from "img/font/3t.png"; //Sandoll 고딕Neo1유니코드
import font4 from "img/font/4.png"; //Sandoll 크림빵
import font4t from "img/font/4t.png"; //Sandoll 크림빵
import font5 from "img/font/5.png"; //Sandoll BOOK
import font5t from "img/font/5t.png"; //Sandoll BOOK

import {
  Paper,
  Typography,
  FormControl,
  FormLabel,
  FormControlLabel,
  RadioGroup,
  Radio,
  Button,
  Icon
} from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";

// 375*665

const styles = theme => {
  return {
    paper: {
      ...theme.mixins.gutters(),
      paddingTop: theme.spacing.unit * 2,
      paddingBottom: theme.spacing.unit * 2,
      width: "860px",
      maxWidth: "100%",
      margin: "20px auto",
      boxSizing: "border-box"
    },
    fontSelect: {
      height: "15px"
    }
  };
};

class PaperBox1 extends Component {
  constructor() {
    super();
    this.state = {
      font: "Noto Sans CJK KR"
    };
  }

  fnFontSelect(e) {
    window.console.log(this.props);
    this.setState({
      ...this.state,
      font: e.target.value
    });
    this.props.setFont(e.target.value);
  }
  render() {
    const { classes } = this.props;
    return (
      <Paper className={classes.paper} elevation={1}>
        <Typography
          style={{ marginBottom: "15px" }}
          variant="h5"
          component="h3"
        >
          # 폰트를 선택해주세요
        </Typography>
        <FormControl component="fieldset" className={classes.formControl}>
          <RadioGroup
            name="gender2"
            className={classes.group}
            value={this.state.font}
            onChange={e => this.fnFontSelect(e)}
          >
            <FormControlLabel
              value="Noto Sans CJK KR"
              control={<Radio color="primary" />}
              label={<img className={classes.fontSelect} src={font1t} />}
            />
            <FormControlLabel
              value="Sandoll 격동고딕"
              control={<Radio color="primary" />}
              label={<img className={classes.fontSelect} src={font2t} />}
            />
            <FormControlLabel
              value="Sandoll 고딕Neo1유니코드"
              control={<Radio color="primary" />}
              label={<img className={classes.fontSelect} src={font3t} />}
            />
            <FormControlLabel
              value="Sandoll 크림빵"
              control={<Radio color="primary" />}
              label={<img className={classes.fontSelect} src={font4t} />}
            />
            <FormControlLabel
              value="Sandoll BOOK"
              control={<Radio color="primary" />}
              label={<img className={classes.fontSelect} src={font5t} />}
            />
          </RadioGroup>
        </FormControl>
      </Paper>
    );
  }
}

const PaperBoxRender1 = withStyles(styles)(PaperBox1);

let startX = 0;
let startY = 0;
let startLeft = 0;
let startTop = 0;
let moveFlag = false;
let boxWidth = 0;
let boxHeight = 0;
let mode = 0;
let startWidth = 0;
let startHeight = 0;

class Edit extends Component {
  constructor() {
    super();
    this.state = {
      font: "Noto Sans CJK KR",
      cursor: "default", //"nwse-resize", "move"
      images: [
        {
          active: 0,
          css: {
            display: "block",
            position: "absolute",
            left: "29%",
            top: "33%",
            width: "42%",
            height: "24%"
          },
          pLeft: 29,
          pTop: 33,
          pWidth: 42,
          pHeight: 24
        }
      ]
    };
  }

  setFont = font => {
    this.setState({
      ...this.state,
      font: font
    });
  };

  setPosition(n, obj) {
    this.setState({
      ...this.state,
      images: [
        {
          ...this.state.images[0],
          pLeft: obj.left,
          pTop: obj.top,
          pWidth: obj.width,
          pHeight: obj.height,
          css: {
            ...this.state.images[0].css,
            left: obj.left + "%",
            top: obj.top + "%",
            width: obj.width + "%",
            height: obj.height + "%"
          }
        }
      ]
    });
  }

  MouseDownWrap = e => {
    // eslint-disable-next-line default-case
    switch (e.target.id) {
      case "image1mask":
        mode = 1;
        this.setState({
          ...this.state,
          images: [
            {
              ...this.state.images[0],
              active: 1
            }
          ]
        });
        break;
      case "ctrResize":
        mode = 2;
        this.setState({
          ...this.state,
          images: [
            {
              ...this.state.images[0],
              active: 1
            }
          ]
        });
        break;
    }
    startX = e.screenX;
    startY = e.screenY;
    startLeft = this.state.images[0].pLeft;
    startTop = this.state.images[0].pTop;
    startWidth = this.state.images[0].pWidth;
    startHeight = this.state.images[0].pHeight;

    let dom = document.getElementById("contentBox");

    boxWidth = dom.clientWidth;
    boxHeight = dom.clientHeight;
  };

  MouseMoveWrap = e => {
    e.preventDefault();
    // console.log("start left", startLeft);
    // console.log("screen x", e.screenX);
    // console.log("startX", startX);
    // console.log("boxWidth", boxWidth);
    // console.log();

    let cursor = "default";

    // eslint-disable-next-line default-case
    switch (e.target.id) {
      case "image1mask":
        if (this.state.cursor !== "move") {
          this.setState({
            ...this.state,
            cursor: "move"
          });
        }
        break;
      case "ctrResize":
        if (this.state.cursor !== "ctrResize") {
          this.setState({
            ...this.state,
            cursor: "nwse-resize"
          });
        }
        break;
      default:
        if (this.state.cursor !== "default") {
          this.setState({
            ...this.state,
            cursor: "default"
          });
        }
        break;
    }

    if (e.nativeEvent.which == 1) {
      // eslint-disable-next-line default-case
      switch (mode) {
        case 1:
          this.setPosition(0, {
            left: ((e.screenX - startX) / boxWidth) * 100 + startLeft,
            top: ((e.screenY - startY) / boxHeight) * 100 + startTop,
            width: this.state.images[0].pWidth,
            height: this.state.images[0].pHeight
          });
          break;
        case 2:
          let x = startX > e.screenX ? startX - e.screenX : e.screenX - startX;
          let y = startY > e.screenY ? startY - e.screenY : e.screenY - startY;

          x = (x / boxWidth) * 100;
          y = (y / boxHeight) * 100;

          let editWidth = startX > e.screenX ? startWidth - x : startWidth + x;
          let editHeight =
            startY > e.screenY ? startHeight - y : startHeight + y;

          this.setPosition(0, {
            left: startLeft,
            top: startTop,
            width: editWidth,
            height: editHeight
          });

          break;
      }
    } else {
      mode = 0;
    }
  };

  render() {
    const { data } = this.props;

    return (
      <>
        <div id="settingBox">
          <PaperBoxRender1 setFont={this.setFont} />
        </div>
        <div
          onMouseDown={this.MouseDownWrap}
          onMouseMove={this.MouseMoveWrap}
          id="editWrap"
          style={{
            cursor: this.state.cursor,
            position: "relative",
            width: "860px",
            height: "860px",
            left: "50%",
            transform: "translateX(-50%)",
            outline: "2px solid #e0e0e0",
            background:
              "linear-gradient(180deg,hsla(0,0%,100%,0) 50%,#fff),linear-gradient(70deg,#eaf5ff 32%,#f6fff8)"
          }}
        >
          <img
            style={{
              position: "absolute",
              left: "50%",
              top: "50%",
              transform: "translate(-50%, -50%)",
              WebkitFilter: "drop-shadow(rgba(0, 0, 0, 0.4) -10px 10px 10px)",
              filter: "drop-shadow(rgba(0, 0, 0, 0.4) -6px 7px 10px)"
            }}
            src={imgBase}
            alt=""
          />
          <div
            id="contentBox"
            style={{
              borderRadius: "40px",
              position: "absolute",
              left: "50%",
              top: "50%",
              width: "35.6%",
              height: "75%",
              transform: "translate(-50%, -50%)",
              overflow: "hidden"
            }}
          >
            <img style={this.state.images[0].css} src={example} alt="" />

            {this.state.font === "Noto Sans CJK KR" && (
              <img
                style={{
                  position: "relative",
                  width: "100%",
                  height: "100%",
                  left: "0px",
                  top: "0px"
                }}
                src={font1}
                alt=""
              />
            )}
            {this.state.font === "Sandoll 격동고딕" && (
              <img
                style={{
                  position: "relative",
                  width: "100%",
                  height: "100%",
                  left: "0px",
                  top: "0px"
                }}
                src={font2}
                alt=""
              />
            )}
            {this.state.font === "Sandoll 고딕Neo1유니코드" && (
              <img
                style={{
                  position: "relative",
                  width: "100%",
                  height: "100%",
                  left: "0px",
                  top: "0px"
                }}
                src={font3}
                alt=""
              />
            )}
            {this.state.font === "Sandoll 크림빵" && (
              <img
                style={{
                  position: "relative",
                  width: "100%",
                  height: "100%",
                  left: "0px",
                  top: "0px"
                }}
                src={font4}
                alt=""
              />
            )}
            {this.state.font === "Sandoll BOOK" && (
              <img
                style={{
                  position: "relative",
                  width: "100%",
                  height: "100%",
                  left: "0px",
                  top: "0px"
                }}
                src={font5}
                alt=""
              />
            )}
          </div>
          <img
            style={{
              position: "absolute",
              left: "34%",
              top: "13.5%"
            }}
            src={imgCamera}
            alt=""
          />
          <div
            style={{
              borderRadius: "40px",
              position: "absolute",
              left: "50%",
              top: "50%",
              width: "35.6%",
              height: "75%",
              transform: "translate(-50%, -50%)"
            }}
          >
            <div
              id="image1mask"
              style={{
                ...this.state.images[0].css,
                outline:
                  this.state.images[0].active === 1
                    ? "1px dashed #bdbdbd"
                    : "none"
              }}
            >
              {this.state.images[0].active === 1 ? (
                <div
                  id="ctrResize"
                  style={{
                    position: "absolute",
                    right: "0px",
                    bottom: "0px",
                    width: "10px",
                    height: "10px",
                    backgroundColor: "#3f51b5",
                    borderRadius: "50%",
                    transform: "translate(50%, 50%)"
                  }}
                />
              ) : (
                ""
              )}
            </div>
          </div>
        </div>
        <div style={{ textAlign: "center", marginTop: "30px" }}>
          <Button size="large" variant="contained" color="primary">
            완성! <Icon style={{ marginLeft: "5px" }}>send</Icon>
          </Button>
        </div>
        <div style={{ padding: "50px" }} />
      </>
    );
  }
}

export default Edit;
